<template>
	<v-app id="home" class="bg-trans">
		<v-container fill-height fluid>
			<v-layout align-center justify-center>
				<v-flex xs6 md4>
						<v-icon id="heart" large color="darken-2">mdi-heart</v-icon>
						<v-card id="card">
							<v-form id="form">
								<v-card-title>
									<v-flex xs12>
										<v-text-field
												v-model="name"
												label="Name"
												outline
										>
										</v-text-field>
									</v-flex>
									<v-flex xs12 >
										<v-btn block color="secondary" dark depressed large @click.prevent="enter">Enter</v-btn>
									</v-flex>
								</v-card-title>
							</v-form>
						</v-card>
				</v-flex>
			</v-layout>
		</v-container>
	</v-app>
</template>

<script>
	import anime from 'animejs';
	import { mapActions } from 'vuex';

	export default {
		name: 'home',
		computed: {
			name: {
				get() { return this.$store.getters.name },
				set(val)  {this.$store.dispatch('updateName', val); }
			}
		},
		mounted () {
			document.getElementById("water").style.bottom = "0";
			document.getElementById("water").style.opacity = "0";
		},
		methods: {
			...mapActions([ 'updateName', 'track' ]),
			enter () {
				anime
					.timeline()
					.add({
						targets: '#form',
						scaleX: 0
					})
					.add({
						targets: '#card',
						keyframes: [
							{
								height: 10
							},
							{
								scaleX: 0,
								scaleY: 0,
								rotate: '5turn',
							}
							// {height: 500}
						],
						duration: 1000,
					})
					.add({
						targets: '#heart',
						keyframes: [
							{
								opacity: 1,
							},
							{
								color: '#ff0000'
							}
						]
						})
					.add({
						targets: '#home',
						color: '#ff0000'
					})
					.add({
						targets: '#heart',
						color: '#2a5298',
					})
					.add({
						targets: '.bg-trans',
						backgroundColor: '#2a5298'
					});

				this.$store.dispatch('track');
				setTimeout(this.navigate, 6000);
			},
			navigate ()
			{
				this.$router.push({ path: "/dreamland" });
			}
		}
	}
</script>

<style lang="scss" scoped>
	#home {
		background-color: black;
	}
	#heart {
		opacity: 0;
		color: white;
		position: fixed;
		top: 48%;
		left: 48.5%;
		}

	#card {
		z-index: 99;
	}

</style>