<template>
    
  <v-app>
    <div id="memo">
      
    <v-carousel
                hide-delimiters 
                height=300 
                class="elevation-0"
                >
      <v-carousel-item v-for="wishes in dreamList"> 
        <div class="dream">
          <div class="wish" @mouseover="cloudout($event.target)">{{ wishes.dream }}</div>
          <div class="author" @mouseover="cloudout($event.target)">~ {{ wishes.name }}</div>
        </div>
      </v-carousel-item>
    </v-carousel>
      <v-icon id="balloon">mdi-airballoon</v-icon>
      <div id="memobook">
       <v-text-field
            v-model="dream"
            id="memopaper"
            placeholder="Don't you want to leave a message? "
            single-line dark
            :append-outer-icon="dream ? 'mdi-sign-direction' : ''"
            @click:append-outer="unleash"
            maxlength=100
          ></v-text-field>
      </div>
    </div>
</v-app>



</template>

<script>
  import anime from 'animejs';
  import { mapActions } from 'vuex';
  import axios from 'axios';

  export default {
    name: 'Dream',
    data() {
      return {
        dreamList: []
      }
    },
    computed: {
      dream: {
        get() { return this.$store.getters.dream },
        set(val)  {this.$store.dispatch('updateDream', val); }
      }
    },
    mounted () {
      axios.get('/dreams.json').then(response => {
        const data = response.data
        for (let key in data){
          const dreamKey = data[key]
          this.dreamList.push(dreamKey)
        }
       }).catch(error => {
        console.log (error);
      })
    },
      methods : 
    {
      ...mapActions([ 'updateDream', 'wish' ]),
      unleash () {
        this.$store.dispatch('wish');
        anime
        .timeline()
        .add({
          targets: '#memobook',
          scaleX: 0,
          duration: 500,
          easing: 'linear'
        })
        .add({
          targets: '#balloon',
          opacity: 1,
          easing: 'linear'
        })
        .add({
          targets: '#balloon',
          translateY: '-1000px',
          duration: 5000,
          easing: 'linear'
        });
      },
      cloudout (cloud)  
      {
        anime({
          targets: cloud,
          opacity: 0,
          skewX: function() 
          { 
          return anime.random(-70, 70); 
          },
          translateY: function() 
          { 
          return anime.random(20, -20) + 'px'; 
          },
          translateX: function() 
          { 
          return anime.random(150, -150) + 'px'; 
          },
          duration: 1000,
          easing: 'linear'
        });
      }
    }
  }
</script>


<style scoped>
#balloon {
  margin-top: 20px;
  margin-right: 10px;
  position: absolute;
  color: white;
  opacity: 0;
  font-size: 36px;
}
#memobook {
  margin: auto 150px;
}
#memopaper {
  text-align: center;
  font-size: 20px;
  background: transparent;
}
.author {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: 24px;
  text-align: right;
  margin-right: 50px;
  margin-bottom: 50px;
}
.wish {
  margin: 100px auto;
  text-align: center;
  width: 90%;
}
#memo {
  text-align: center;
  margin-top: 50px;
  background: transparent;
  border: none;
  font-family: 'Pacifico', cursive;
  color: white;
}

.dream {
  font-size: 25px;
}
</style>    