import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
      name: '',
      dream: ''
  },
  getters: {
      name: state =>  state.name ,
      dream: state =>  state.dream
  },
  mutations: {
    updateName: (state, payload) => {
      state.name = payload;
    },
    updateDream: (state, payload) => {
      state.dream = payload;
    }
  },
  actions: {
    updateName({commit}, payload) {
      commit('updateName', payload);
    },
    updateDream({commit}, payload) {
      commit('updateDream', payload);
    },
    track() {
      axios.post('/visitors.json', {
        name: this.state.name
      }).then(response => {
        console.log ('Your name is sent into the clouds with response: ' + response);
      }).catch(error => {
        console.log (error);
      })
    },
    wish() {
      if (this.state.name == '') {
        this.state.name = 'Anonymous';
      }
      if (this.state.dream == '') {
        this.state.dream = "I'm a lazy anonymous with no dream who clicked submit without typing. No wonder why I am ugly.";
      }

      axios.post('/dreams.json', {
        name: this.state.name,
        dream: this.state.dream
      }).then(response => {
        console.log ('Your dream is sent into the clouds with response: ' + response);
      }).catch(error => {
        console.log (error);
      })
    }
  }
})
